/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}

			.mkdf-pli-rating-holder {
				margin: 0;
			}
		}

		.mkdf-pli-inner {
			.mkdf-pli-text-inner {
				.mkdf-pli-price {
					position: relative;
					top: -2px;
				}
			}
		}
	}

    .mkdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .mkdf-pli-title {
		    margin: 0 0 3px;
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 9px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();
	        position: absolute;
	        top: -7px;
	        right: 0;
	        margin: 0;
	        font-size: 16px;
	        line-height: 34px;
	        font-weight: 500;
	        letter-spacing: .06em;
	
	        span{
		        line-height: 34px;
	        }
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-pli-text.mkdf-pli-hover-overlay {
                opacity: 1;
            }

            .mkdf-pli-text.mkdf-pli-button-from-bottom .mkdf-button,
            .mkdf-pli-text.mkdf-pli-button-from-bottom .added_to_cart{
        		@include mkdfTransform(translateY(0));
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();
	        @supports (-ms-ime-align: auto) {
		        width: 99.9%;
	        }

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            box-sizing: border-box;
	
	        .button{
		        &.add_to_cart_button{
			        padding: 8px 42px;
			        font-size: 10px;
			        font-weight: 500;
		        }
	        }
	        
            .mkdf-button,
        	.added_to_cart{
        		white-space: normal;
        	}

            &.mkdf-pli-hover-overlay{
            	padding: 10px 15px;
	            opacity: 0;
	            background-color: rgba($default-background-color, 0.9);
                @include mkdfDefaultTransition(opacity);
            }

            &.mkdf-pli-button-from-bottom{

            	.mkdf-pli-text-inner{
            		vertical-align: bottom;
            	}

            	.mkdf-button,
            	.added_to_cart{
            		width: 100%;
            		padding-left: 10px;
            		padding-right: 10px;
            		@include mkdfTransform(translateY(100%));
            		@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            	}
            }
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #000;
            }
	        	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include mkdfButtonSolidColor();
				        font-size: 10px;

				        &:hover {
				            @include mkdfButtonSolidHoverColor();
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonSmallParams();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
	    margin: 19px 0 25px;
        text-align: left;
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.mkdf-standard-layout {
		
		.mkdf-pli {
			float: left;
		}
		
		&.mkdf-large-space {
			margin: 0 0 -50px;
		}
		
		&.mkdf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.mkdf-small-space {
			margin: 0 0 -20px;
		}
		
		&.mkdf-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($standard_list_columns) {
			&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.mkdf-pli {
					width: $column_width;

					@if ($i > 2) { // set different break point for five and six columns
						@media only screen and (min-width: $laptop-landscape-mac-plus-pixel) {

							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if ($i == 2) { // set different break point for four columns
						@media only screen and (min-width:  $ipad-landscape-plus-pixel) {

							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else {
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {

							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pl-outer {
			opacity: 0;
		}
		
		.mkdf-pl-sizer {
			width: 100%;
		}
		
		.mkdf-pl-gutter {
			width: 0;
		}
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.mkdf-pli.mkdf-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.mkdf-pli,
				.mkdf-pl-sizer {
					width: $column_width;
				}
				
				.mkdf-pli.mkdf-woo-image-large-width,
				.mkdf-pli.mkdf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/*************** Filter - start ***************/

	.mkdf-pl-categories{
		display: inline-block;
		vertical-align: top;
		width: 49%;
		text-align: left;

		ul{
			list-style-type: none;
			li{
				display: inline-block;
				vertical-align: middle;

				&:not(:last-child) {
					margin-right: 15px;
				}

				a.active{
					color: $first-main-color;
				}
			}
		}
	}

	.mkdf-pl-ordering-outer{
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 49%;
		text-align: right;

		.mkdf-pl-ordering-holder{
			display: inline-block;
			vertical-align: middle;

			&:hover{
				.mkdf-pl-ordering {
					opacity: 1;
				}
			}

			.mkdf-pl-ordering-filter-title{
				cursor: pointer;

				&:after{
					content: '\43';
					font-family: 'ElegantIcons';
				}
			}
		}
		
		.mkdf-pl-ordering{
			position: absolute;
			opacity: 0;
			right: 0;
			color: #fff;
			background-color: #010101;
			text-align: left;
			padding: 20px 30px;
			@include mkdfDefaultTransition(opacity);
			z-index: 150;

			.mkdf-pl-ordering-inner{
				display: inline-block;
				vertical-align: top;
				min-width: 150px;
			}

			ul{
				list-style-type: none;
			}

			a{
				color: #ababab;

				&.active,
				&:hover{
					color: #fff;
				}
			}

			.mkdf-pl-ordering-title{
				color: inherit;
			}
		}
	}

	.mkdf-prl-loading{
		position: absolute;
		top: 0;
		left: 0;
		display: none;
		width: 100%;
		height: 100%;
		text-align: center;
		background-color: #fff;
		z-index: 200;

		.mkdf-prl-loading-msg{
			display: inline-block;
			vertical-align: middle;
			margin-top: 200px;
		}
	}

	/*************** Filter - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */