/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 56px;
            width: 58px;
            padding: 0;
            margin: 0 35px 0 35px;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 22px;
            height: 56px;
            line-height: 54px;
            border: none;
            font-size: 20px;
            cursor: pointer;
            @include mkdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }
    
        .mkdf-quantity-minus{
            left: 0;
        }

        .mkdf-quantity-plus {
            top: 2px;
            right: -2px;
            border-bottom: 0;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        padding: 17px 26px;
        font-family: inherit;
        font-size: 16px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid $default-border-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;
        border-radius: 0;
        @extend %input-style;

        &:focus {
            border-color: $default-heading-color;
        }
    
        @include mkdfPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            padding: 12px 26px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */