/* ==========================================================================
   Product List - Simple shortcode styles - begin
   ========================================================================== */

.mkdf-pls-holder {
	@include mkdfRelativeHolderLayout();

	> ul {
		@include mkdfRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;

		> li {
			@include mkdfRelativeHolderLayout();
			padding: 0;
			margin: 0 0 20px;

			&:last-child {
				margin: 0;
			}
		}
	}
	
	.mkdf-pls-image {
		display: table-cell;
		vertical-align: top;
		width: 120px;

		a, img {
			display: block;
		}

		a {
			position: relative;
			@include mkdfImageOverlayHoverStyle();
		}
	}
	
	.mkdf-pls-text {
		display: table-cell;
		vertical-align: top;
		padding: 0 0 0 20px;
		
		.mkdf-pls-title {
			margin: 0 0 4px;
		}
		
		.mkdf-pls-rating-holder {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 5px 0 3px;
		}
		
		.mkdf-pls-rating {
			@include mkdfWooCommerceRatingsStyle();
		}
		
		.mkdf-pls-price {
			@include mkdfWooCommercePriceStyle();
		}
	}
}
/* ==========================================================================
   Product List - Simple shortcode styles - end
   ========================================================================== */