/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 130px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 110px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 39px 0 39px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 19px;
        float: left;
        box-sizing: border-box;
	
	    .single_add_to_cart_button{
			@include mkdfButtonMediumParams( !important);
	    }
	    
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 11px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
			ins{
				font-size: 22px;
				font-weight: 400;
			}

			* {
				font-size: 20px;
				line-height: 1.5em;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 13px 0 0;
			.star-rating{
				color: #dddcdc;
			}
		    
		    .woocommerce-review-link{
			    display: none;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 43px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 45px 0 0;
		
		    > span {
			    display: block;
			    margin: 0;
				color: $default-heading-color;
				font-weight: 400;
				font-size: 15px;
				letter-spacing: 0;
			    line-height: 1.867em;
				span, a{
					color: $default-text-color;
					font-size: 15px;
					font-weight: 400;
					line-height: 2.067em;
				}
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
			margin: 10px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		    > span {
			    padding: 0 9px 0 0;
				color: $default-heading-color;
				font-weight: 700;
				font-size: 13px;
				letter-spacing: 0.05em;
				text-transform: uppercase;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 39px 0 -7px;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 48px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 44px 0 0;
			.mkdf-quantity-input{
				font-style: normal;
				color: $default-heading-color;
				font-size: 16px;
				font-weight: 400;
			}
			span{
				&.mkdf-quantity-plus,
				&.mkdf-quantity-minus{
					color: $default-heading-color;
					font-size: 20px;
				}
			}
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 101px 0 0;
		
		.woocommerce-Tabs-panel {
			.shop_attributes {
				tr{
					th{
						display: block;
						margin: 0;
						color: #000;
						font-weight: 400;
						font-size: 15px;
						letter-spacing: 0;
						line-height: 1.867em;
					}
					
					td{
						color: #6c6c6c;
						font-size: 15px;
						font-weight: 400;
						line-height: 1em;
					}
				}
			}
		}
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				border: 1px solid #dddcdc;
				
				&.active{
					background-color: $first-main-color;
					border-color: $first-main-color;
					a{
						color: $default-heading-color;
					}
				}
				
				a {
					font-size: 13px;
					font-weight: 500;
					text-transform: uppercase;
					letter-spacing: 0.06em;
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 19px 29px 17px;
					color: $default-heading-color;
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 20px 15px 20px 0;
			}
		}
		
		table {
			margin: 20px 15px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 400;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			padding: 17px 15px 20px 0;
			box-sizing: border-box;
			
			h2 {
				font-size: 30px;
				line-height: 24px;
				margin: 8px 0 48px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 100px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 123px;
					
					p {
						margin: 0;
						.woocommerce-review__published-date{
							font-size: 15px;
							line-height: 1.6em;
							font-weight: 400;
							color: #6c6c6c;
							-webkit-font-smoothing: antialiased;
							font-family: $default-text-font;
							letter-spacing: .06em;
						}
						
						.woocommerce-review__author{
							font-family: $default-text-font;
							color: #000;
							font-weight: 500;
							text-transform: uppercase;
							font-size: 20px;
							line-height: 1.2em;
							letter-spacing: .06em;
						}
					}
					
					.description{
						margin: 14px 0 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					font-family: $default-text-font;
					color: #000;
					font-weight: 500;
					text-transform: uppercase;
					font-size: 20px;
					line-height: 1.2em;
					letter-spacing: .06em;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
				
				.form-submit{
					.submit{
						padding: 12px 48px;
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 41px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 23px;
			text-align: left;
			font-size: 30px;
			font-weight: 500;
			line-height: 2em;
			letter-spacing: 0.05em;
			
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				text-align: left;
				margin: 12px 0 -2px;
				
				h3{
					font-size: 20px;
					font-weight: 300;
					letter-spacing: 0;
					line-height: 1.5em;
					text-transform: none;
					text-align: left;
					float: left;
					margin-bottom: -6px;
					max-width: 55%;
				}
				
				.price{
					position: absolute;
					top: -7px;
					right: 0;
					margin: 0;
					font-size: 20px;
					line-height: 1.5em;
					font-weight: 300;
					letter-spacing: 0;
					
					span{
						line-height: 1.7em;
					}
				}
				
				.mkdf-pl-rating-holder{
					margin: 0;
				}
			}
			.star-rating{
				color: #dddcdc;
			}
		}

	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */