/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    margin: 48px 0 66px;
    clear: both;

    @include ipad-landscape {
        margin: 48px 0 126px;
    }

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: center;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a:not(.prev):not(.next),
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                line-height: 1.875em;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                vertical-align: middle;
                margin: 0;
                padding: 0 14px;
                color: #000;
                font-size: 16px;
                font-weight: 400;
                @include mkdfTransition(color .2s ease-out);
    
                &:after{
                    position: relative;
                    display: block;
                    min-width: 11px;
                    bottom: 7px;
                    content: '';
                    height: 1px;
                    background-color: $default-heading-color;
                   @include mkdfTransform(scaleX(0));
                    @include mkdfTransformOrigin(50% 0);
                    @include mkdfTransition(transform .3s ease-out);
                }
                
                &:hover{
                    color: $default-heading-color;
                }
                
                &.current {
                    color: $default-heading-color;
                    
                    &:after{
                       @include mkdfTransform(scaleX(1));
                        @include mkdfTransformOrigin(50% 0);
                        @include mkdfTransition(transform .3s ease-out);
                    }
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    vertical-align: middle;
                    margin: 0 6px 0 4px;
                    font-family: 'Linearicons-Free';
                    speak: none;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    color: $default-heading-color;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;

                    &:before, &:after {
                        position: relative;
                        top: 0;
                        display: block;
                        font-size: 22px;
                        line-height: inherit;
	                    @include mkdfTransform(scaleX(1));
                        @include mkdfTransition(left .3s $default-easing-svg-arrows);
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
                    margin-right: 62px;
                    &:before {
                        content: "\e875";
                        display: inline-block;
                        left: 0;
                    }
                    &:after {
                        content: "\e875";
                        display: inline-block;
                        position: absolute;
                        left: 27px;
                    }

                    &:hover {
                      &:before {
                        left: -6px;
                      }
                    }
                }
    
                &.next {
                    margin-left: 22px;
                    &:before {
                        content: "\e876";
                        display: inline-block;

                    }
                    &:after {
                        content: "\e876";
                        display: inline-block;
                        position: absolute;
                        left: 9px;
                    }

                    &:hover {
                        &:after {
                            left: 14px;
                        }
                    }
                }
            }
            
            &:hover{
            a:not(.prev):not(.next), span{
                &:after{
                    @include mkdfTransform(scaleX(1));
                    @include mkdfTransformOrigin(50% 0);
                    @include mkdfTransition(transform .3s ease-out);
                }
            }
            }
        }
    }
}

.mkdf-woo-view-all-pagination {
    position: relative;
    display: none;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 17px;
        color: $default-text-color;

        &:before {
            content: '';
            padding: 0 28px 0 0;
            border-left: 1px solid $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */