/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    display: block;
    position: absolute;
    top: 20px;
    width: 99px;
    color: $default-heading-color;
    background-color: $first-main-color;
    font-size: 15px;
    line-height: 1;
    letter-spacing: 0;
    text-align: center;
    box-sizing: border-box;
    margin: 0;
    padding: 10px 20px;
    z-index: 100;
    @include mkdfTransform(translateZ(0));
}

@mixin mkdfWooCommerceSaleStyle() {
    right: 0;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    right: 0;
}

@mixin mkdfWooCommerceNewProductStyle() {
    right: 0;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 0;
    line-height: 1;
    color: #373737;
    
    * {
        font-size: 16px;
        line-height: 1.6em;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
        span{
            font-size: 14px;
        }
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 18px;
    line-height: 1;
    letter-spacing: 8px;
    color: #dddcdc;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f3ae\f3ae\f3ae\f3ae\f3ae";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f2fc\f2fc\f2fc\f2fc\f2fc";
            display: block;
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f3ae";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f2fc\f2fc\f2fc\f2fc\f2fc";
        position: absolute;
        right: 0;
        bottom: 3px;
        display: block;
        @extend %star-ratings;
    }

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        &.active{
            &:after{
                width: 111px;
            }
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */