/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 24px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }

        .mkdf-cart-number {
            font-size: 0.750em;
            background-color: $first-main-color;
            color: $default-heading-color;
            height: 14px;
            width: 14px!important;
            display: inline-block;
            line-height: 14px;
            text-align: center;
            border-radius: 100%;
            position: relative;
            font-weight: 500;
            bottom: -9px;
            right: 12px;
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }

    .mkdf-cart-number {
        background-color: $header-dark-color;
        color: #fff;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }

    .mkdf-cart-number {
        background-color: #fff;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 314px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-shadow: $menu-box-shadow;
    box-sizing: border-box;
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 40px 40px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 0 0 20px;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: $default-text-color;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 60px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding: 0 10px;

        > span {
            color: $default-text-color;
        }

        .mkdf-product-title {
            display: block;
            margin: 0;
            box-sizing: border-box;
            font-size: 18px;
            letter-spacing: 0;
            font-weight: 400;
            text-transform: initial;
        }

        .remove {
            position: absolute;
            top: -2px;
            right: -5px;
	        font-size: 21px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: inline-block;
            margin: 0;
            font-size: 15px;
            letter-spacing: 0;
        }

        .amount {
            display: inline-block;
            margin: 0;
            font-size: 15px;
            letter-spacing: 0;
        }
    }

    .mkdf-cart-bottom {
        padding: 6px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 10px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }
            .mkdf-total{
                font-size: 15px;
                font-weight: 400;
                letter-spacing: 0;
                color: $default-text-color;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 16px;
                font-weight: 500;
                letter-spacing: 0.06em;
            }
        }

        .mkdf-view-cart,
        .mkdf-checkout {
            display: block;
            float: left;
            height: 40px;
            line-height: 41px;
            padding: 0 19px;
            font-size: 10px;
            font-weight: 500;
	        letter-spacing: 2px;
            color: $default-heading-color;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                background-color: $secondary-main-color;
            }
        }

        .mkdf-checkout {
            float: right;
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */